import allCotent from '../json/allContent.json';
import React, { useEffect, useState } from 'react';
function Contact() {
    const [loader, setloader] = useState(false)
    const [state, setState] = React.useState({
        nameT: " ",
        mail: " ",
        number: " ",
        msg: " "
    })

    const postData=()=>{
        fetch("http://nauth.ksofttechnology.com/API/AUTH", {
            method: 'post',
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({

                TYPE: "AUTH",
                NAME: "GET_AUTH_TOKEN",
                STR: [
                    {
                        A_ID: "18785869",
                        U_ID: "pslv",
                        PWD: "000000",
                        MODULE: "B2B",
                        HS: "D"
                    }
                ]

            })
        }).then(res => res.json())
            .then((result) => {
                setloader(true)
                console.log(result)
            })
            .catch((Error) => {
                console.log(Error)
            })
    }
    useEffect(() => {
        // postData()

    })
    const handleChange = (e) => {

        setState({ ...state, [e.target.name]: e.target.value })

    }
    const getFormData = (e) => {
        e.preventDefault();
        console.log(state)
        alert(state.nameT)
    }

    return (
        <>
            <div className="container-fluid" style={{
                backgroundColor: '#212529',
                height: '800px'
            }} >
                <div className="row" style={{ paddingTop: '10rem', textAlign: 'center' }}>
                    <div className="col-md-2 pt-5 mt-5 ">

                    </div>
                    <div className="col-md-8 pt-5 mt-5 ">
                        <h2 >
                            <span style={{ color: 'white' }}>You can directly contact with me on this number : </span>
                            <span style={{ color: 'white' }}>{allCotent[4].data.contact_no}</span>
                        </h2>
                        <h4 >
                            <span style={{ color: 'white' }}>Get in touch via the form below, or by emailing : </span>
                            <span style={{ color: 'white' }}>{allCotent[4].data.Email_id}</span>
                        </h4>
                    </div>



                    <div className="col-md-8 pt-5 mt-5 ">
                        <form onSubmit={getFormData}>
                            <input name="nameT" onChange={handleChange} required></input>
                            <input name="mail" onChange={handleChange} required></input>
                            <input name="number" onChange={handleChange} required></input>
                            <input name="msg" onChange={handleChange} required></input>
                            <button type='submit'>Submit</button>
                        </form>
                    </div>


                </div>
            </div>
        </>
    )
}
export default Contact;